<template>
	<view class="box">
			<view class="back-home" :style="{'top':min_top+'px'}" @tap="backHome"></view>
		<navigator url="register" class="go-register" :style="{'top':min_top+'px'}">新用户，先注册</navigator>
		<view class="login-main">
			<view class="login-tips font-bold">
				Hello，你好
			</view>
			<view class="login-box">
				<button class="confirm-btn" open-type="getUserInfo" @getuserinfo="wxLogin" :disabled="logining">微信授权登录</button>
				<view class="rule">
					<view class="iconfont iconlujing" v-if="!is_check" @tap="is_check=!is_check"></view>
					<view class="iconfont iconxuanzhong" v-if="is_check" @tap="is_check=!is_check"></view>
					我已经阅读并同意 <navigator class="url" url="/pages/user/rule">《用户服务协议》</navigator>及<navigator class="url" url="/pages/user/privacy">《隐私政策》</navigator>
				</view>
			</view>
			<view class="other-login">
				<view class="title">
					其他登录方式
				</view>
				<view class="login-type">
					<navigator url="login" class="iconfont iconwode"></navigator>
				</view>
			
			</view>
		</view>
	</view>

</template>

<script>
	import jwt_decode from "jwt-decode";
	import {
		getProvider,
		getCode
	} from '@/common/js/login/newlogin.js'
	let provider=""
	export default {
		data() {
			return {
				logining: false,
				info_res: {},
				code: "",
				min_top:100,
				is_check:true
			};
		},
		onLoad() {
			// #ifdef MP
			 let menuButtonInfo = uni.getMenuButtonBoundingClientRect();
			 this.min_top=menuButtonInfo.height+menuButtonInfo.top+20;
			 // #endif
			(async () => {
				//获取服务商信息
				provider = await getProvider();
				//获取code
				this.code = await getCode(provider[0])
			})()
		},
		methods: {
			backHome() {
				uni.setStorageSync('setStatusIndexFunc', 0)
				uni.reLaunch({
					url: '/pages/index'
				})
			},
			wxLogin(e) {
				if(!this.is_check){
					this.$box.toast('请先阅读并同意用户协议', 'none');
					return;
				}
				// #ifndef H5
				let that = this;
				if (!this.code) {
					uni.showToast({
						icon: 'none',
						title: '正在加载中，稍等一下',
						duration: 2000
					});
				}
				if (e.detail && e.detail.errMsg == 'getUserInfo:ok') {
					uni.getUserInfo({
						success(info_res) {
							that.info_res = info_res;
							that.sendLogin()
						}
					})
				} 
				// #endif
			},
			sendLogin() {
				this.$api.post('/login/save', {
					code: this.code,
					rawData: this.info_res.rawData,
					iv: this.info_res.iv,
					signature: this.info_res.signature,
					encryptedData: this.info_res.encryptedData
				}).then(res => {
					if (res.code == 200) {
						let token = res.data;
						let user_json =jwt_decode(token);
						this.$common.setLocal('token', res.data);
						this.$common.setLocal('customerSeqId', user_json.userId);
						this.$common.setLocal('customerName', user_json.userName || "");
						this.$store.commit('saveUser',user_json)
						uni.setStorageSync('setStatusIndexFunc', 0)
						uni.reLaunch({
							url:'/pages/index'
						})
					}
				})
			}
		}
	}
	
	  
</script>
​
<style lang="scss">
	.rule {
		display: flex;
		align-items: center;
		font-size: 20rpx;
		.iconfont {
			font-size: 30rpx;
			padding: 8rpx;
		}
		.iconxuanzhong {
			color: $color-primary;
		}
		.url {
			color: $color-primary;
		}
	}
.back-home {
		position: absolute;
		top: 50rpx;
		left: 50rpx;
		color: #000;
		font-size: 30rpx;
		width: 40rpx;
		height: 40rpx;
		background: url('@/static/images/home.png') no-repeat;
		background-size: 100% 100%;
	}
	.confirm-btn {
		background: $color-primary;
		margin: 60rpx 0;
		height: 80rpx;
		line-height: 80rpx;
		border-radius: 100rpx;
		font-size: 30rpx;
		padding: 0;
		font-weight: bold;
		color: white;
		text-align: center;
		cursor: pointer;

	}
	.iconshouye-xiaoxi {
		font-size: 32rpx !important;
	}
	
	.login-main {
		width: 100%;
	}
	
	.bg-primary {
		background: $color-primary;
		margin: 60rpx 0;
	}
	
	.box {
		width: 100%;
		height: 100vh;
		background: white;
		display: flex;
		align-items: center;
		justify-content: center;
	
		.go-register {
			position: absolute;
			top: 100rpx;
			right: 40rpx;
			font-size: 30rpx;
			color: $color-primary;
		}
	}
	
	.login-tips {
		width: calc(100% - 80rpx);
		margin: 0 auto;
		font-size: 60rpx;
		margin-bottom: 70rpx;
	}
	
	.login-box {
		width: calc(100% - 80rpx);
		margin: 0 auto;
	
		.login-line {
			display: flex;
			width: 100%;
			align-items: center;
			height: 100rpx;
			line-height: 100rpx;
			font-size: 26rpx;
	
			.title {
				width: 130rpx;
				flex-shrink: 0;
			}
	
			.input {
				font-size: 26rpx;
				flex-grow: 1;
			}
		}
	}
	
	.yzm-btn {
		padding: 12rpx 20rpx;
		margin-left: 10rpx;
		height: 26rpx;
		line-height: 26rpx;
		flex-shrink: 0;
	}
	
	.other-login {
		width: 100%;
		font-size: 30rpx;
		width: 100%;
		text-align: center;
		color: #a1a1a1;
		margin-top: 60rpx;
	
		.iconfont {
			font-size: 60rpx;
			margin: 0 20rpx;
			margin-top: 40rpx;
		}
	
		.login-type {
			display: flex;
			justify-content: center;
			align-items: center;
		}
	}
</style>
